import * as d3 from 'd3';
import * as _ from 'lodash';

let nodes = [{ "id": 1, "key": "p-uc", "title": "用户中心", "description": "用户中心", "createdAt": "2016-11-08T06:14:30.000Z", "updatedAt": "2016-11-09T02:29:22.000Z" }, { "id": 2, "key": "p-longguo", "title": "龙果", "description": "龙果", "createdAt": "2016-11-08T06:15:35.000Z", "updatedAt": "2016-11-09T02:29:41.000Z" }, { "id": 4, "key": "p-cms", "title": "cms", "description": null, "createdAt": "2016-11-09T02:30:33.000Z", "updatedAt": "2016-11-09T02:30:33.000Z" }, { "id": 5, "key": "p-trade", "title": "支付中心", "description": "支付中心", "createdAt": "2016-11-09T02:30:48.000Z", "updatedAt": "2016-11-09T02:30:48.000Z" }, { "id": 6, "key": "p-erp", "title": "erp", "description": "erp", "createdAt": "2016-11-09T02:31:01.000Z", "updatedAt": "2016-11-09T02:31:01.000Z" }, { "id": 7, "key": "p-crm", "title": "crm", "description": "crm", "createdAt": "2016-11-09T02:31:11.000Z", "updatedAt": "2016-11-09T02:31:11.000Z" }, { "id": 8, "key": "p-hongmei", "title": "红美主站", "description": "红美主站", "createdAt": "2016-11-09T02:32:06.000Z", "updatedAt": "2016-11-09T02:32:06.000Z" }, { "id": 9, "key": "p-longyan", "title": "龙眼", "description": "龙眼", "createdAt": "2016-11-09T02:32:25.000Z", "updatedAt": "2016-11-09T02:32:25.000Z" }, { "id": 10, "key": "p-jiazhuang", "title": "家装", "description": "家装", "createdAt": "2016-11-09T05:51:14.000Z", "updatedAt": "2016-11-09T05:51:14.000Z" }, { "id": 11, "key": "p-dts", "title": "dts", "description": "dts", "createdAt": "2016-11-09T05:51:32.000Z", "updatedAt": "2016-11-09T05:51:32.000Z" }];
let links = [];

export class D3Controller {
    static $inject = ["$stateParams"];

    private simulation: any;
    private link: any;
    private node: any;

    constructor(private $stateParams: ng.ui.IStateParamsService) {
        this.drawSuits();
    }

    doTrans() {

    }

    drawSuits() {
        const width = innerWidth || Math.max(960, innerWidth),
            height = innerHeight || Math.max(500, innerHeight);
        let svg = d3.select("#homed3")
            .attr("width", width)
            .attr("height", height)

        let color = d3.scaleOrdinal(d3.schemeCategory20);
        let simulation = this.simulation = d3.forceSimulation()
            .force("link", d3.forceLink().id((d: { name: string }): string => { return d.name; }))
            .force("charge", d3.forceManyBody().strength(-300))
            .force("center", d3.forceCenter(width / 3, height / 2));

        let link = this.link = svg.append("g")
            .attr("class", "links")
            .selectAll("line")
            .data(links)
            .enter()
            .append("line")
            .style("stroke", (d, i: any): string => {
                return color(i);
            })
            .attr("stroke-width", (d) => { return Math.sqrt(1.2) });

        let dragFunc = d3.drag()
            .on("start", dragstarted)
            .on("drag", dragged)
            .on("end", dragended);

        let node = this.node = svg.append("g")
            .attr("class", "nodes")
            .selectAll("circle")
            .data(nodes)
            .enter()
            .append("circle")
            .attr("r", 5)
            .style("fill", (d, i: any): string => {
                return color(i);
            })
            .call(dragFunc)
            .on("mouseover", function (d, i) {
                d3.select(this)
                    .style("fill", "yellow");
            }).on("mouseout", function (d, i) {
                d3.select(this)
                    .transition()
                    .duration(500)
                    .style("fill", function (d, i: any) { return color(i); });
            });

        simulation.nodes(nodes)
            .on("tick", ticked)
            .force("link", d3.forceLink(links).distance(100));

        function dragstarted(d) {
            if (!d3.event.active) simulation.alphaTarget(0.5).restart();
            d.fx = d.x;
            d.fy = d.y;
        }

        function dragged(d) {
            d.fx = d3.event.x;
            d.fy = d3.event.y;
        }

        function dragended(d) {
            if (!d3.event.active) simulation.alphaTarget(0);
            d.fx = null;
            d.fy = null;
        }

        function ticked() {
            link
                .attr("x1", function (d: any) { return d.source.x; })
                .attr("y1", function (d: any) { return d.source.y; })
                .attr("x2", function (d: any) { return d.target.x; })
                .attr("y2", function (d: any) { return d.target.y; });

            node
                .attr("cx", function (d: any) { return d.x; })
                .attr("cy", function (d: any) { return d.y; });
        }
    }
}


